@import './styles/variables';

.App {
  margin: 0 auto;
  text-align: center;
  background-color: whitesmoke;
  max-width: 375px;
  width: 100%;
  font-family: 'Rubik', sans-serif;
  line-height: 24px;
  display: flex;
  flex-direction: column;
  font-size: 18px;
  padding: 0 30px;

  h2 {
    font-weight: 500;
    font-size: 32px;
    line-height: normal;
  }

  p {
    color: $grayish;
    margin: 20px 0 30px;
    line-height: 1.5;
    text-align: start;
  }
}

.logo {
  height: 50px;
}

.button {
    background-color: $lightBlue;
    outline: transparent;
    padding: 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 4px;
    border: none;
    transition: background-color 0.3s ease;
    color: white;
  }

@media (min-width: 768px) {
  .App {
    margin: 0 auto;
    text-align: center;
    background-color: whitesmoke;
    max-width: 1440px;
    width: 100%;
    font-family: 'Rubik', sans-serif;
    line-height: 24px;
    display: flex;
    flex-direction: column;
    font-size: 18px;
    padding: 0 $desktop-margin;
  }
}
